<template>
  <main>

    
    <div class="flex_main">
      <div class="title_login text-center mb-auto pt-5 mt-5">
        <h1 class="text-center p-2">Hello!</h1>
        <h1 class="text-center">My Test Messenger</h1>
      </div>
      <section class="forms" v-show="showRegister">
        <form class="register text-center" @submit.prevent="register">
          <div class="title_start">
            <h2>
              Register
              <span>or&nbsp;</span>
              <span @click="showRegister = !showRegister">LOGIN IN</span>
            </h2>
          </div>
          <div class="input_start">
            <input
              v-model="register_form.email"
              type="email"
              placeholder="Email address"
            />
          </div>
          <div class="input_start">
            <input
              v-model="register_form.password"
              type="password"
              placeholder="Password"
            />
          </div>
          <div class="submit_start">
            <input type="submit" value="Register" />
          </div>
        </form>
        <form class="login"></form>
      </section>
      <section class="forms" v-show="!showRegister">
        <form class="login text-center" @submit.prevent="login">
          <div class="title_start">
            <h2>
              LOG IN
              <span>or &nbsp;</span>
              <span @click="showRegister = !showRegister">SIGN UP</span>
            </h2>
          </div>
          <div class="input_start">
            <input
              v-model="login_form.email"
              type="email"
              placeholder="Email address"
            />
          </div>
          <div class="input_start">
            <input
              v-model="login_form.password"
              type="password"
              placeholder="Password"
            />
          </div>
          <div class="submit_start">
            <input type="submit" value="Login" />
          </div>
        </form>
        <form class="login"></form>
      </section>
    </div>
    <div >loader</div>
  </main>
</template>

<script>
import { ref } from "vue";
import { useStore } from "vuex";
//import { auth } from "../firebase";


export default {
  setup() {
    const login_form = ref({});
    const register_form = ref({});
    const store = useStore();

    const login = () => {
      store.dispatch("login", login_form.value);
    };
    const register = () => {
      store.dispatch("register", register_form.value);
    };
    return {
      login_form,
      register_form,
      login,
      register,
    };
  },
  data() {
    return {
      showRegister: false,
      showAuth: null
    };
  },

  
    // showForm() {
    //   console.log();
    //   if (this.$route.path ==='/login' && auth.currentUser) {
    //     console.log('showForm' ,true);
    //     return true;
    //   }
    //   console.log('showForm' ,false);
    //   return false;
    // },
  
};
</script>

